body {
  display: flex;
  flex-direction: column;
  width: 100%;
  font-family: "Arial", sans-serif;
  color: @main-text-color;
  margin: 0;
  background-color: @main-background-color;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
